import React, { useState } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import Youtubeimg from './images/youtube.svg'
import { Typography } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import MicIcon from '@mui/icons-material/Mic';
import VideoCallOutlinedIcon from '@mui/icons-material/VideoCallOutlined';
import AppsOutlinedIcon from '@mui/icons-material/AppsOutlined';
import NotificationsNoneOutlinedIcon from '@mui/icons-material/NotificationsNoneOutlined';
import { Avatar } from '@mui/material';
import Doyin from './images/doyin.jpg';
import { Link } from 'react-router-dom';



function Navbar() {
    const [inputSearch, setInputSearch] = useState('');


    return (
        <div className="navbar">
            <div className="left">
                <div className="menu">
                    <MenuIcon />
                </div>
                <Link to="/">
                
                <div className="logo">
                    <img src={Youtubeimg} alt="log" />
                    <Typography variant="h5">Youtube</Typography><sup>NG</sup>
                </div>
                </Link>
            </div>
            <div className="middle">
                <input onChange={e => setInputSearch(e.target.value)} value={inputSearch} type="search" placeholder="Search" />
               <Link to={`/search/${inputSearch}`}>
               <SearchIcon className="search-click"/>

               </Link>
            </div>
            <div className="right">
                <MicIcon className="hide" />
                <VideoCallOutlinedIcon />
                <AppsOutlinedIcon />
                <NotificationsNoneOutlinedIcon className="hide" />
                <Avatar alt="gmail" src={Doyin} />
            </div>

        </div>
    )
}

export default Navbar
